<template>
  <div class="category">
    <h1>Каталог айпадов</h1>
    <a-row gutter="16">
      <a-col v-for="item in ipads" :key="item.id" :xs="24" :sm="12" :md="8">
        <a-card :title="item.name" hoverable class="product-card">
          <img :src="item.image" alt="iPad" style="width: 100%; height: 300px; object-fit: cover;" />
          <p>{{ item.description }}</p>
          <strong>{{ item.price }} ₽</strong>
          <a-button type="primary" block style="margin-top: 8px;" @click="addToCart(item)">
            В корзину
          </a-button>
        </a-card>
      </a-col>
    </a-row>
  </div>
</template>

<script>
export default {
  name: 'IpadsCategory',
  data() {
    return {
      ipads: [
        {
          id: 1,
          name: 'Apple iPad Air 13',
          description: 'Apple iPad Air 13 M2 (2024) 256Gb Wi-Fi +Сellular',
          price: 114990,
          image: require('@/assets/ipad13.jpg')
        },
        {
          id: 2,
          name: 'Apple iPad mini',
          description: 'Apple iPad mini (2024) 128Gb Wi-Fi +Сellular',
          price: 79990,
          image: require('@/assets/ipadmini.jpg')
        }
      ]
    }
  },
  methods: {
    addToCart(product) {
      // Получаем корзину из localStorage или создаём пустую
      let cart = JSON.parse(localStorage.getItem('cart')) || []

      // Проверяем, есть ли товар в корзине
      const exists = cart.find(item => item.id === product.id)

      if (!exists) {
        // Если товара нет, добавляем его в корзину
        cart.push(product)
        localStorage.setItem('cart', JSON.stringify(cart))  // Сохраняем корзину в localStorage
        this.$message.success(`Товар добавлен в корзину: ${product.name}`)
      } else {
        // Если товар уже есть в корзине
        this.$message.warning('Товар уже в корзине')
      }
    }
  }
}
</script>

<style scoped>
.category {
  padding: 40px;
}

.product-card {
  margin-bottom: 16px;
}
</style>
